<template>
  <table class="c-table" :style="styles">
    <tr
      v-for="(trData, index) in data"
      :key="index"
      :class="getTrClass(index)"
      class="c-table__tr"
    >
      <td
        v-for="(td, tdIndex) in trData"
        :key="tdIndex"
        class="c-table__tr__td"
        :class="{ 'c-table__tr__td--first': tdIndex === 0 }"
        :style="{
          width: `${parseInt(480 / trData.length)}px`,
          height: index !== 0 ? tdMaxHeight + 'px' : '22px'
        }"
      >
        {{ td }}
      </td>
    </tr>
  </table>
</template>
<script>
import Handsontable from 'handsontable'
import { isArray } from '@/libs/jsTypeUtil'

export default {
  name: 'Ctable',
  props: {
    data: {
      type: Array,
      default() {
        return Handsontable.helper.createEmptySpreadsheetData(8, 10)
      },
      validator(value) {
        return isArray(value) && value.every(item => isArray(item))
      }
    },
    width: {
      type: [Number, String],
      default: null
    },
    height: {
      type: [Number, String],
      default: null
    }
  },
  data() {
    return {
      tdMaxHeight: 22
    }
  },
  computed: {
    styles() {
      const style = {}
      if (this.height) {
        const height = parseInt(this.height)
        style.height = `${height}px`
      }
      if (this.width) style.width = `${this.width}px`
      return style
    },
    // 开发table-body部分的tr等高的需求
    // 因为现在col时等宽的，所以直接用二维数组的最长字符串判断
    longestStrcell() {
      return !isArray(this.data)
        ? { str: '', pos: [0, 0] }
        : this.data.reduce(
            (acc, rowArr, rowIndex) => {
              let _acc = Object.assign({}, acc)
              rowArr.forEach((cell, colIndex) => {
                const $cell = typeof cell !== 'string' ? '' : cell
                if (colIndex === 0 && $cell.length > _acc.length) {
                  _acc = {
                    str: $cell,
                    posX: rowIndex,
                    posY: colIndex
                  }
                }
                if ($cell.length > _acc.str.length) {
                  if (
                    colIndex > 0 &&
                    $cell.length > rowArr[colIndex - 1].length
                  ) {
                    _acc = {
                      str: $cell,
                      posX: rowIndex,
                      posY: colIndex
                    }
                  }
                }
              })
              return _acc
            },
            { str: '', posX: 0, posY: 0 }
          )
    }
  },
  watch: {
    longestStrcell: {
      handler: function(val) {
        this.$nextTick(() => {
          this.tdMaxHeight = 22
          setTimeout(() => {
            const targetTrDom = document.querySelector(
              `.c-table__tr-${val.posX}`
            )
            if (targetTrDom) this.tdMaxHeight = targetTrDom.offsetHeight
          }, 100)
        })
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    getTrClass(index) {
      return `c-table__tr-${index} ` + (index === 0 ? 'c-table__tr--first' : '')
    }
  }
}
</script>
<style lang="scss" scoped type="text/scss">
table {
  opacity: 0.9;
  filter: alpha(opacity=50);
}
.c-table {
  max-width: none;
  max-height: none;
  line-height: 1.2;
  border-width: 0;
  box-sizing: border-box;
  border-spacing: 0;
  border-collapse: separate;
  table-layout: fixed;
  outline-width: 0;
  cursor: default;
  &__tr {
    &--first {
      .c-table__tr__td {
        border-top: 2px solid #715845;
      }
    }
    &__td {
      padding: 2px;
      text-align: center;
      border-right: 2px solid#715845;
      border-bottom: 2px solid #715845;
      word-break: break-all;
      word-wrap: break-word;
      &--first {
        border-left: 2px solid#715845;
      }
    }
  }
}
</style>
