<template>
  <div class="monitor">
    <el-dialog
      :visible.sync="viewerModal"
      :modal-append-to-body="false"
      scrollable
      :width="String(modalWidth)"
      :z-index="zIndex"
      :fullscreen="fullscreen"
      title="显示器"
      class="monitor__modal"
      :class="`monitor__modal--${monitorInfoType}`"
      @opened="initInlineViewer"
    >
      <div
        v-if="monitorInfoType === 'image' && !fullscreen"
        slot="title"
        class="monitor__modal__header"
      >
        <span class="monitor__modal__header__title">显示器：</span>
        <el-button
          v-for="item in viewerModalSizeArr"
          :key="item"
          :type="item === curSize ? 'primary' : 'default'"
          class="monitor__modal__size-btn"
          size="small"
          @click="changeSize(item)"
          >{{ item | viewerModalSizeFilter }}</el-button
        >
      </div>
      <inlineViewer
        v-if="monitorInfoType === 'image'"
        ref="inlineViewer"
        v-model="inlineViewerImageUrl"
        :image-url-arr="currentImageUrlArr"
        :height="modalHeight"
        :class="curSize"
      />
      <video
        v-if="viewerModal && monitorInfoType === 'video'"
        :src="videoUrl"
        width="100%"
        controls="controls"
      />
      <div
        v-if="viewerModal && monitorInfoType === 'call'"
        class="monitor__modal__audio-warpper"
      >
        <audio :src="audioUrl" width="100%" autoplay controls />
      </div>
    </el-dialog>
  </div>
</template>

<script>
import inlineViewer from './inline-viewer'

export default {
  filters: {
    viewerModalSizeFilter: function(val) {
      return { large: '大', default: '中', small: '小' }[val]
    }
  },
  components: {
    inlineViewer
  },
  props: {
    needRefresh: {
      type: Boolean,
      default: false
    },
    monitorInfo: {
      type: Object,
      required: true,
      validator: function(value) {
        return (
          ['image', 'video', 'call'].indexOf(value.type) > -1 ||
          JSON.stringify(value) === '{}'
        )
      }
    },
    fullscreen: {
      type: Boolean,
      default: false
    },
    currentImageUrlArr: {
      type: Array,
      default: () => []
    },
    size: {
      type: String,
      default: 'default',
      validator: function(value) {
        return ['large', 'small', 'default'].indexOf(value) > -1
      }
    },
    zIndex: {
      type: Number,
      default: 1000
    }
  },
  data() {
    return {
      inlineViewerImageUrl: '',
      viewerModal: false,
      viewerModalSizeArr: ['large', 'default', 'small'],
      curSize: 'default',
      videoUrl: '',
      audioUrl: '',
      monitorInfoType: '',
      videoWidth: 300,
      modelDefaultWidth: 300
    }
  },
  computed: {
    viewerModalWidth() {
      return { large: 640, default: 520, small: 400 }[this.curSize]
    },
    modalWidth() {
      if (this.fullscreen) return NaN
      else {
        switch (this.monitorInfoType) {
          case 'video':
            return this.videoWidth
          case 'image':
            return this.viewerModalWidth
          default:
            return this.modelDefaultWidth
        }
      }
    },
    modalHeight() {
      return this.modalWidth * 0.8
    },
    modalStyle() {
      return this.monitorInfoType === 'call'
        ? { top: 0, left: window.screen.width - this.modalWidth }
        : {}
    }
  },
  watch: {
    monitorInfo: {
      handler: function({ type, meta }, oldVal) {
        this.monitorInfoType = type
        if (!type || !meta) {
          return
        }
        switch (type) {
          case 'image':
            this.inlineViewerImageUrl = meta.url
            break
          case 'video':
            if (oldVal.type === 'image') this.$refs.inlineViewer.hide()
            this.intVideoDom(meta)
            break
          case 'call':
            if (oldVal.type === 'image') this.$refs.inlineViewer.hide()
            this.intAudioDom(meta)
            break
          default:
            throw new Error(
              '传入显示器的信息type不再当前显示器处理的范围，type：' + type
            )
        }
      },
      deep: true
    },
    size: function(val) {
      this.curSize = val
    },
    curSize: function(val) {
      this.$refs.inlineViewer.intViewer()
    }
  },
  methods: {
    // 初始化inline-viewer
    initInlineViewer() {
      if (this.needRefresh) {
        if (this.$refs.inlineViewer) {
          this.$refs.inlineViewer.intViewer()
        }
      }
    },
    changeSize(targetSize) {
      this.curSize = targetSize
    },
    viewerReset() {
      this.inlinecImageUrl = ''
      this.curSize = 'default'
      this.viewerModal = false
      this.$refs.inlineViewer.int()
    },
    show() {
      this.viewerModal = true
    },
    getVideoModalWidth(width, height) {
      const DomMaxSize = 600
      if (width >= height) {
        return width < 300 ? 300 : width > DomMaxSize ? DomMaxSize : width
      } else {
        return (DomMaxSize * width) / height
      }
    },
    intVideoDom(meta) {
      this.videoUrl = meta.url
      if (meta.width && meta.height) {
        this.videoWidth = this.getVideoModalWidth(
          Number(meta.width),
          Number(meta.height)
        )
      }
    },
    intAudioDom(meta) {
      this.audioUrl = meta.url
    }
  }
}
</script>

<style lang="scss" scoped>
.monitor {
  &__modal {
    &--call {
      .ivu-modal-content-drag {
        top: 100px;
        left: 300px;
      }
    }
    &__header {
      display: flex;
      align-items: center;
      &__title {
        font-size: 14px;
        font-weight: bold;
        color: #17233d;
      }
    }
    &__size-btn + &__size-btn {
      margin-left: 10px;
    }
    .ivu-modal-body {
      padding: 0;
      line-height: 0;
    }
    &__audio-warpper {
      display: flex;
      align-items: center;
      height: 100px;
      background: #333;
    }
  }
}
</style>
