<template>
  <el-select
    :value="value"
    :placeholder="placeholder"
    @change="onValueChange"
    clearable
    :size="size"
    :disabled="disabled"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :value="item.value"
      :label="item.label"
      >{{ item.label }}</el-option
    >
  </el-select>
</template>
<script>
import { getOrderType } from '@/api/order'
export default {
  props: {
    optionsData: {
      type: Array,
      default: () => []
    },
    size: {
      type: String,
      default: 'small'
    },
    label: {
      type: String,
      default: '订单类型'
    },
    placeholder: {
      type: String,
      default: '请选择订单类型'
    },
    value: [String, Boolean],
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    getRemoteData: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    optionsData() {
      this.setData(this.optionsData)
    },
    getRemoteData(val) {
      if (val) {
        getOrderType().then(res => {
          this.options = res.data
        })
      }
    }
  },
  mounted() {
    this.setData(this.optionsData)
  },
  data() {
    return {
      options: []
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    },
    setData(arr) {
      if (arr.length) {
        this.options = arr
      }
    }
  }
}
</script>
