<template>
  <!-- crm模块，客户类型 -->
  <el-form-item :label="moduleName" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      @change="changeValue"
      :placeholder="placeholder"
      clearable
      :disabled="disabled"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
import { getUserTypeOptionApi } from '@/api/crmSetting'
export default {
  data() {
    return {
      options: []
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    isOpen: [String, Boolean],
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  created() {
    this.getCustomerTypeOptionFun()
  },
  watch: {
    isOpen(val) {
      this.getCustomerTypeOptionFun()
    }
  },
  methods: {
    getCustomerTypeOptionFun() {
      let params = {}
      if (this.isOpen !== true && this.isOpen !== false) {
        params = {}
      } else {
        params = { is_open: this.isOpen }
      }
      getUserTypeOptionApi(params).then(res => {
        if (res) {
          this.options = res.data
        }
      })
    },
    changeValue(val) {
      this.$emit('input', val)
    }
  }
}
</script>
