<template>
  <div ref="galley" class="inline-viewer" :style="`height: ${height}px`">
    <img v-for="(url, index) in curImageUrls" :key="url + index" :src="url" />
  </div>
</template>

<script>
import Viewer from 'viewerjs'
export default {
  model: {
    prop: 'imageUrl',
    event: 'change'
  },
  props: {
    imageUrl: {
      type: String,
      default: '',
      required: true
    },
    imageUrlArr: {
      type: Array,
      default: () => []
    },
    height: {
      type: Number,
      default: 300
    }
  },
  data() {
    return {
      curViewer: null,
      curImageUrls: []
    }
  },
  computed: {
    initialViewIndex() {
      return this.curImageUrls.indexOf(this.imageUrl) || 0
    }
  },
  watch: {
    imageUrl: function(val) {
      // 给imageUrlArr添加默认imageUrl
      if ((!this.imageUrlArr || this.imageUrlArr.length === 0) && val) {
        this.curImageUrls = [val]
      } else if (val) {
        setTimeout(res => {
          this.viewGo()
        }, 1000)
      }
    },
    imageUrlArr: {
      handler: function(val) {
        this.updateCurImageUrls()
      },
      immediate: true
    }
  },
  created() {
    // 不需要提前更新数据，这里直接用的传值
  },
  mounted() {
    // 在这里intViewer是没有意义的， curImageUrls跟新了，但是inlineViewer没有现实， vue不会渲染imgDom
  },
  methods: {
    intViewer() {
      this.viewDestroy()
      this.$nextTick(() => {
        if (!this.$refs.galley) console.log('垃圾')
        this.curViewer = new Viewer(this.$refs.galley, {
          className: 'monitor-inline-viewer',
          url: 'src',
          inline: true, // inline = true时, this.curViewer.hide() 失效，可以看源码
          initialViewIndex: this.initialViewIndex,
          viewed: () => {
            this.$emit('change', this.curImageUrls[this.curViewer.index])
          }
        })
      })
    },
    updateCurImageUrls() {
      // 限制预览的个数
      // if (this.curImageUrls.length > 20) this.curImageUrls = []
      // this.intViewer()
      // const newAddImageUrlArr = this.imageUrlArr.filter(url => this.curImageUrls.indexOf(url) < 0)
      // this.curImageUrls = this.curImageUrls.concat(newAddImageUrlArr)
      // this.curImageUrls = newAddImageUrlArr
      this.intViewer()
      this.curImageUrls = []
      this.curImageUrls = this.imageUrlArr
    },
    viewGo(isShow) {
      this.$nextTick(() => {
        // setTimeout(res => {
        this.curViewer.update().view(this.initialViewIndex)
        if (isShow) {
          const viewerDom = document.querySelector(
            '.inline-viewer + .viewer-container'
          )
          viewerDom.style.display = 'block'
        }
        // }, 700)
      })
    },
    viewDestroy() {
      if (this.curViewer) {
        this.curViewer.destroy()
        // 防止再次intViewer的时候出现两个viewer
        this.curViewer = null
      }
    },
    async show() {
      if (
        !this.curViewer ||
        !this.imageUrlArr ||
        this.imageUrlArr.length === 0
      ) {
        this.intViewer()
        return
      }
      this.viewGo(true)
    },
    hide() {
      if (this.curViewer) {
        const viewerDom = document.querySelector(
          '.inline-viewer + .viewer-container'
        )
        viewerDom.style.display = 'none'
      }
    },
    int() {
      this.viewDestroy()
      this.curImageUrls = []
    }
  },
  destroy() {
    this.viewDestroy()
  }
}
</script>
<style lang="scss" scoped type="text/scss">
.inline-viewer {
  min-height: 400px;
  text-align: center;
  img {
    display: none;
  }
}
</style>
