<template>
  <div class="dialog" :class="diaStyle">
    <el-dialog
      :visible.sync="tipShow"
      :close-on-click-modal="modelClose"
      :width="width"
      :before-close="handleCancel"
      top="30vh"
      :class="closeHid ? 'close-hidden' : ''"
      :center="tipCenter"
      @close="$emit('update:tipShow', false)"
    >
      <section slot="title">
        <h6 class="center">{{ title }}</h6>
        <slot name="headTip" />
      </section>
      <slot name="form"></slot>
      <slot name="content" />
      <span v-if="hasFooter" slot="footer" class="dialog-footer">
        <el-button v-if="showCancelBtn" @click="$emit('cancel')">
          {{ cancelText }}
        </el-button>
        <el-button
          v-if="hasConfirmBtn"
          type="primary"
          @click="$emit('btnHandle')"
        >
          {{ btnText }}
        </el-button>
        <p v-if="footerTitle" class="footer-title">{{ footerTitle }}</p>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { setTimeout } from 'timers'
export default {
  model: {
    prop: 'tipShow',
    event: 'cancel'
  },
  props: {
    showCancelBtn: {
      type: Boolean,
      default: false
    },
    tipCenter: {
      type: Boolean,
      default: false
    },
    tipShow: {
      type: Boolean,
      default: false
    },
    btnText: {
      type: String,
      default: '确定'
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    diaStyle: {
      type: String,
      default: ''
    },
    time: {
      type: [String, Number],
      default: ''
    },
    hasFooter: {
      type: Boolean,
      default: true
    },
    hasConfirmBtn: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: '提示'
    },
    footerTitle: {
      type: String,
      default: ''
    },
    closeHid: {
      type: Boolean,
      default: false
    },
    modelClose: {
      type: Boolean,
      default: true
    },
    width: {
      type: String,
      default: '25%'
    }
  },
  data() {
    return {
      timer: null
    }
  },
  created() {
    const self = this
    if (this.time) {
      const time = Number(this.time)
      this.timer = setTimeout(function() {
        self.$emit('btnHandle')
      }, time)
    }
  },
  destroyed() {
    this.timer = null
  },
  methods: {
    handleCancel() {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog {
  .center {
    font-size: 16px;
  }
  /deep/.el-dialog__body {
    padding: 12px 20px;
  }
  &.dia-style/deep/.el-dialog__body {
    padding: 8px 20px;
    border-bottom: 1px solid #e8eaec;
  }
  &.pfr/deep/.el-dialog {
    margin-top: 0 !important;
  }
  /deep/.el-dialog__footer {
    text-align: center;
  }
  .footer-title {
    margin-top: 10px;
    font-size: 12px;
    color: #999;
    &::before {
      margin-right: 4px;
      color: #f56c6c;
      content: '*';
    }
  }
  /deep/button {
    padding: 6px 20px;
    height: 32px;
    &.el-dialog__headerbtn {
      top: 10px;
      right: 5px;
    }
  }
  .close-hidden {
    /deep/.el-dialog__headerbtn {
      display: none;
    }
  }
}
</style>
