<template>
  <el-select
    :value="value"
    @change="onValueChange"
    size="small"
    clearable
    multiple
    collapse-tags
    filterable
    placeholder="请选择订单名称"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :value="item.value"
      :label="item.label"
      >{{ item.label }}</el-option
    >
  </el-select>
</template>

<script>
import { getOrderNames } from '@/api/order'
import _ from 'lodash'
export default {
  name: 'OrderNamesSelect',
  props: {
    value: [String, Array],
    orderType: {
      type: String,
      default: ''
    },
    optionsData: {
      type: Array,
      default: () => []
    },
    getRemoteData: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      options: []
    }
  },
  watch: {
    optionsData() {
      this.getData()
    },
    orderType() {
      this.getData()
    }
  },
  mounted() {
    if (this.getRemoteData) {
      getOrderNames().then(res => {
        this.options = res.data
      })
    }
  },
  methods: {
    getData() {
      if (this.optionsData.length) {
        if (this.orderType && this.orderType !== '0') {
          let arr = _.filter(this.optionsData, o => {
            return o.order_type === this.orderType
          })
          this.options = arr
        } else {
          this.options = this.optionsData
        }
      }
    },
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
