<template>
  <div
    class="my_form two-form"
    :style="'width: ' + (form.width ? form.width : '100%')"
    :class="{ alignleft: form.isLeft === true }"
  >
    <!-- 标题 -->
    <div class="form-title" v-if="form.showTitle">{{ form.title }}</div>
    <!-- 通用表单 -->
    <el-form
      :class="{ showFormBorder: !!form.showFormBorder }"
      v-if="!form.isTable"
      :ref="myRef ? myRef : form.ref"
      :disabled="formDisabled"
      :label-position="form.labelPositon"
      :size="form.formSize ? form.formSize : 'mini'"
      :model="formData"
      enctype="multipart/form-data"
      @submit.native.prevent
      :label-width="form.labelWidth"
    >
      <!-- input  -->
      <el-row>
        <template v-for="(item, index) in formItem">
          <el-col
            :span="!item.colSpan ? 24 / form.column - 1 : item.colSpan"
            :key="index"
            v-if="item.show"
          >
            <el-form-item
              v-if="showItem(item) && !item.haveItem"
              :label="!!item.label ? item.label : ''"
              :prop="item.name"
              :rules="item.rule !== 'phone' ? item.rules : phoneVal"
              :label-width="
                item.labelWidth
                  ? item.labelWidth
                  : !!form.labelWidth
                  ? form.labelWidth
                  : '100%'
              "
            >
              <!-- 输入框 -->
              <el-input
                v-if="item.type === 'text'"
                v-model.trim="formData[item.name]"
                :type="item.type"
                :placeholder="item.placeholder"
                :maxlength="item.maxlength"
                :show-word-limit="item.showlimit ? item.showlimit : false"
                clearable
                :disabled="item.disabled"
              >
                <template v-if="form.style === 'prepend'" slot="prepend">
                  {{ item.label }}
                </template>
              </el-input>
              <!-- html文本 -->
              <div v-else-if="item.type === 'html'">
                <p v-html="item.value"></p>
              </div>
              <div v-else-if="item.type === 'slot'">
                <slot :name="item.slotName"></slot>
              </div>
              <div v-else-if="item.type === 'tipRow'">
                <p>{{ formData[item.name] }}</p>
              </div>
              <!-- 文本提示 -->
              <div v-else-if="item.type === 'tip'">
                <p class="tip-right">{{ item.value }}</p>
              </div>
              <!-- 日期选择框 -->
              <el-date-picker
                v-else-if="item.type === 'date'"
                v-model="formData[item.name]"
                :type="item.type"
                :placeholder="item.placeholder"
                :default-time="item.defaultTime"
                :picker-options="
                  item.showBefore
                    ? showBeforTime
                    : item.showAfter
                    ? pickerOptions
                    : {}
                "
                :disabled="item.disabled"
                :format="item.format ? item.format : 'yyyy-MM-dd'"
                :style="'width: ' + (item.width ? item.width : '100%')"
              ></el-date-picker>
              <!-- 时间选择框 -->
              <el-time-picker
                v-else-if="item.type === 'fixed-time'"
                v-model="formData[item.name]"
                :type="item.type"
                :placeholder="item.placeholder"
                :default-time="item.defaultTime"
                :disabled="item.disabled"
                :format="item.format ? item.format : 'hh:mm:ss'"
                :style="'width: ' + (item.width ? item.width : '100%')"
              ></el-time-picker>
              <!-- 日期时间选择器 -->
              <el-date-picker
                v-else-if="item.type == 'datetime'"
                v-model="formData[item.name]"
                :type="item.type"
                :placeholder="item.placeholder"
                :default-time="item.defaultTime"
                :align="item.align"
                :picker-options="pickerOptions"
                :disabled="item.disabled"
                :value-format="
                  item.format ? item.format : 'yyyy-MM-dd hh:mm:ss'
                "
                :format="item.format ? item.format : 'yyyy-MM-dd hh:mm:ss'"
                :style="'width: ' + (item.width ? item.width : '100%')"
              ></el-date-picker>
              <!-- 日期时间范围选择器 -->
              <el-date-picker
                v-else-if="item.type == 'datetimerange'"
                v-model="formData[item.name]"
                type="datetimerange"
                range-separator="至"
                :disabled="item.disabled"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              ></el-date-picker>
              <!-- 开关 -->
              <el-switch
                v-else-if="item.type == 'switch'"
                v-model="formData[item.name]"
              ></el-switch>
              <!-- select选择框 -->
              <el-select
                v-else-if="item.type == 'select'"
                v-model="formData[item.name]"
                :placeholder="item.placeholder"
                :multiple="item.multiple"
                :collapse-tags="item.collapseTags"
                default-first-option
                :filterable="item.filterable"
                value-key="id"
                :disabled="item.disabled"
                :allow-create="item.allowCreate"
                clearable
                @changeValue="selectChange($event, item.name)"
                :style="'width: ' + (item.width ? item.width : '100%')"
              >
                <el-option
                  v-for="iitem in item.options"
                  :key="iitem.id"
                  :label="iitem.label"
                  :value="iitem.value"
                ></el-option>
              </el-select>
              <!-- 复选框 -->
              <el-checkbox-group
                v-else-if="item.type == 'checkbox'"
                v-model="formData[item.name]"
                :style="'width: ' + (item.width ? item.width : '100%')"
              >
                <el-checkbox
                  v-for="iitem in item.options"
                  :key="iitem.value"
                  :label="iitem.value"
                  >{{ iitem.label }}</el-checkbox
                >
              </el-checkbox-group>
              <!-- 单选 -->
              <el-radio-group
                v-else-if="item.type == 'radio'"
                v-model="formData[item.name]"
              >
                <el-radio
                  v-for="(option, idx) in item.options"
                  :key="idx"
                  :label="option.value"
                  :disabled="!!option.disabled ? option.disabled : false"
                  >{{ option.label }}</el-radio
                >
              </el-radio-group>
              <!--单选按钮组 -->
              <el-radio-group
                v-else-if="item.type == 'radioGroup'"
                :disabled="item.disabled"
                v-model="formData[item.name]"
              >
                <el-radio-button
                  v-for="(option, idx) in item.options"
                  :key="idx"
                  :label="option.value"
                  >{{ option.label }}</el-radio-button
                >
              </el-radio-group>
              <!-- 文本域 -->
              <el-input
                v-else-if="item.type == 'textarea'"
                :type="item.type"
                :maxlength="item.maxlength"
                :show-word-limit="item.showlimit"
                v-model="formData[item.name]"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
              ></el-input>
              <service-package
                v-else-if="item.type === 'servicePackage'"
                :optionsData="productTypesOpts"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              ></service-package>
              <!-- 计数器 -->
              <el-input-number
                v-else-if="item.type == 'inputnumber'"
                v-model="formData[item.name]"
                :min="item.min ? item.min : -Infinity"
                :max="item.max ? item.max : Infinity"
                :disabled="item.disabled"
                :precision="item.precision ? item.precision : 0"
                :step="item.step ? item.step : 1"
                :step-strictly="item.stepStrictly"
                :size="item.size"
                :controls-position="item.controlsPosition"
              >
              </el-input-number>
              <OrderStatusSelect
                v-else-if="item.type === 'orderStatus'"
                v-model="formData[item.name]"
                :optionsData="orderStatusOpts"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </OrderStatusSelect>
              <OrderNameSelect
                v-else-if="item.type === 'orderName'"
                v-model="formData[item.name]"
                :optionsData="orderNamesOpts"
                :orderType="item.orderType"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </OrderNameSelect>
              <signTypeSelect
                v-else-if="item.type === 'signType'"
                v-model="formData[item.name]"
                :optionsData="orderActionsOpts"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </signTypeSelect>
              <OrderTypeSelect
                v-else-if="item.type === 'orderType'"
                v-model="formData[item.name]"
                :optionsData="orderTypesOpts"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </OrderTypeSelect>
              <serviceDepartment
                v-else-if="item.type === 'serviceDepartment'"
                v-model="formData[item.name]"
                :optionsData="serviceDepartmentOpts"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </serviceDepartment>
              <servicePeople
                v-else-if="item.type === 'servicePeople'"
                :optionsData="servicePeopleOpts"
                :peopleType="item.peopleType"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </servicePeople>
              <WeappSceneSelect
                v-else-if="item.type === 'weappScene'"
                :optionsData="weappSceneOpts"
                :peopleType="item.peopleType"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </WeappSceneSelect>
              <customSceneSelect
                v-else-if="item.type === 'customScene'"
                :optionsData="customSceneOpts"
                :peopleType="item.peopleType"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </customSceneSelect>
              <fdStatusSelect
                v-else-if="item.type === 'fdStatus'"
                :optionsData="fdStatusOpts"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </fdStatusSelect>
              <!-- 其他 -->
              <el-input
                v-else-if="item.type == 'password'"
                :type="item.type"
                :placeholder="item.placeholder"
                v-model="formData[item.name]"
                clearable
                @keyup.enter.native="submitForm()"
              >
                <template v-if="form.style === 'prepend'" slot="prepend">
                  {{ item.label }}
                </template>
              </el-input>
              <div
                v-else-if="
                  item.type === 'btn' &&
                    !!form.hasSubmit &&
                    form.btnStyle === 'inline'
                "
                class="clearfix"
                style="min-width: 210px;max-width:215px;"
              >
                <el-button
                  v-if="form.showExportBtn"
                  type="primary"
                  :class="form.btnRight ? 'float-right' : ''"
                  size="mini"
                  class="margin-left-10"
                  :disabled="form.exportBtnDisabled"
                  @click="exportBtnFun()"
                  >导出</el-button
                >
                <el-button
                  v-if="!!form.cancleShow"
                  :class="form.btnRight ? 'float-right' : ''"
                  size="mini"
                  class="margin-left-10"
                  @click="resetForm()"
                  >{{ form.cancleText }}</el-button
                >
                <el-button
                  type="primary"
                  :class="form.btnRight ? 'float-right' : ''"
                  size="mini"
                  :disabled="form.submitDisabled"
                  @click="submitForm()"
                  >{{ form.submitText }}</el-button
                >
              </div>
              <el-input
                v-else
                :type="item.type"
                :placeholder="item.placeholder"
                v-model="formData[item.name]"
              ></el-input>
              <!-- 是否显示单位 -->
              <span v-if="item.unit" style="margin-left:1px;">
                {{ item.unit }}
              </span>
            </el-form-item>
            <!-- 存在el-form-item -->
            <div v-if="item.haveItem">
              <div
                class="operator_btn"
                v-if="item.type === 'btn' && showItem(item)"
              >
                <el-button
                  type="primary"
                  :class="item.btnRight ? 'float-right' : ''"
                  size="mini"
                  class="margin-left-20"
                  @click="handle(item.handleName, item)"
                  :disabled="item.disabled"
                >
                  {{ item.btnTxt }}
                </el-button>
              </div>
              <SourceChannelSelect
                v-if="item.type === 'sourceChannel'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :rules="item.rules"
                :multiple="item.multiple"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :isOpen="item.isOpen ? item.isOpen : 'all'"
                @changeValue="selectChange($event, item.name)"
              ></SourceChannelSelect>
              <UserTypeSelect
                v-else-if="item.type === 'customerType'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :rules="item.rules"
                :isOpen="item.isOpen"
              ></UserTypeSelect>
              <OpratorActionSelect
                v-else-if="item.type === 'opratorAction'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :isOpen="item.isOpen ? item.isOpen : 'all'"
                :rules="item.rules"
              ></OpratorActionSelect>
              <CreatorSelect
                v-else-if="item.type === 'creator'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :rules="item.rules"
              ></CreatorSelect>
              <ContractStatusSelect
                v-else-if="item.type === 'contractStatus'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :rules="item.rules"
              ></ContractStatusSelect>
              <RelatedStatusSelect
                v-else-if="item.type === 'relatedStatus'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :prop="item.name"
                :placeholder="item.placeholder"
                :disabled="item.disabled ? item.disabled : false"
                :rules="item.rules"
              ></RelatedStatusSelect>
              <SupervisorSelect
                v-else-if="item.type === 'supervisor'"
                :moduleName="item.label"
                v-model="formData[item.name]"
                :disabled="item.disabled ? item.disabled : false"
                :prop="item.name"
                :placeholder="item.placeholder"
                :rules="item.rules"
              >
              </SupervisorSelect>
            </div>
            <!-- 是否显示tooltip提示 -->
            <el-tooltip
              v-if="item.tooltip && item.tooltip.show"
              effect="dark"
              class=""
              :content="item.tooltip.content"
              placement="top"
            >
              <i
                class="form_tooltip"
                :class="item.tooltip.icon ? item.tooltip.icon : 'el-icon-info'"
              >
              </i>
            </el-tooltip>
            <!-- 是否显示tip提示 -->
            <div
              v-if="item.tip && item.tip.show"
              :class="item.tipClass ? item.tipClass : 'inline-tip'"
            >
              {{ item.tip.content }}
            </div>
          </el-col>
        </template>
      </el-row>
    </el-form>
    <!-- 表格式表单 -->
    <el-table
      v-else
      :data="form.data"
      :show-header="true"
      :border="true"
      style="width: 100%"
    >
      <el-table-column
        v-for="(item, index) in form.column"
        :key="index"
        :prop="item.prop"
        :label="item.label"
        width="auto"
      >
        <template slot-scope="scope">
          <el-input
            size="mini"
            v-model="scope.row[item.prop]"
            :disabled="item.disabled"
          ></el-input>
        </template>
      </el-table-column>
    </el-table>
    <el-row
      v-if="!!form.hasSubmit && !form.btnStyle"
      class="btn-group clearfix"
      :class="{ 'btn-group-top100': !!form.istop100 }"
    >
      <el-button
        type="primary"
        :class="form.btnRight ? 'float-right' : ''"
        size="mini"
        class="margin-left-20"
        @click="submitForm()"
        :disabled="form.submitDisabled"
      >
        {{ form.submitText }}
      </el-button>
      <el-button
        v-if="!!form.cancleShow"
        :class="form.btnRight ? 'float-right' : ''"
        size="mini"
        @click="resetForm()"
      >
        {{ form.cancleText }}
      </el-button>
    </el-row>
  </div>
</template>
<script>
import RelatedStatusSelect from '@/components/crm/SelectComponents/RelatedStatusSelect.vue'

import UserTypeSelect from '@/components/crm/SelectComponents/UserTypeSelect.vue'
import OpratorActionSelect from '@/components/crm/SelectComponents/OpratorActionSelect.vue'
import CreatorSelect from '@/components/crm/SelectComponents/CreatorSelect.vue'
import ContractStatusSelect from '@/components/crm/SelectComponents/ContractStatusSelect.vue'
import SourceChannelSelect from '@/components/crm/SelectComponents/SourceChannelSelect.vue'
import SupervisorSelect from '../crm/SelectComponents/SupervisorSelect'
import ServicePackage from '@/components/SearchItem/ServicePackage'
import OrderTypeSelect from '@/components/Select/OrderTypeSelect'
import OrderNameSelect from '@/components/Select/OrderNamesSelect'
import OrderStatusSelect from '@/components/Select/OrderStatusSelect'
import WeappSceneSelect from '@/components/Select/WeappSceneSelect'
import CustomSceneSelect from '@/components/Select/CustomSceneSelect'

import signTypeSelect from '@/components/Select/signTypeSelect'
import serviceDepartment from '@/components/Select/serviceDepartment'
import servicePeople from '@/components/Select/servicePeople'
import fdStatusSelect from '@/components/Select/fdStatusSelect'
import { getOrderOpts } from '@/api/order'
import _ from 'lodash'
export default {
  name: 'MyForm',
  props: {
    fullscreenLoading: {
      type: Boolean,
      default: true
    },
    form: {
      type: Object,
      default: () => {
        return {
          showExportBtn: {
            type: Boolean,
            default: false
          },
          submitDisabled: {
            type: Boolean,
            default: false
          },
          formSize: {
            type: String,
            default: 'mini'
          },
          btnStyle: {
            type: String,
            default: ''
          },
          btnRight: {
            type: Boolean,
            default: false
          },
          cancleShow: {
            type: Boolean,
            default: true
          },
          isLeft: {
            type: Boolean,
            default: true
          },
          title: {
            type: String,
            default: '标题'
          },
          showTitle: {
            type: Boolean,
            default: true
          },
          ref: {
            type: String,
            default: 'form'
          },
          width: {
            type: Number,
            default: 100
          },
          isTable: {
            type: Boolean,
            default: false
          },
          labelWidth: {
            type: String,
            default: '120px'
          },
          labelPositon: {
            type: String,
            default: 'right'
          },
          column: {
            type: Number,
            default: 1
          },
          hasSubmit: {
            type: Boolean,
            default: true
          },
          disabled: {
            type: Boolean,
            default: false
          }
        }
      }
    },
    formItem: {
      type: Array,
      default: () => [Object]
    },
    formDisabled: {
      type: Boolean,
      default: false
    },
    myRef: {
      type: String,
      default: ''
    },
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    getOrderOptsData: {
      type: Boolean,
      default: false
    }
  },

  data() {
    var validPhone = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入电话号码'))
      } else if (!this.isvalidPhone(value)) {
        callback(new Error('请输入正确的11位手机号码'))
      } else {
        callback()
      }
    }
    return {
      defaultMSG: null,
      checkAll: false,
      isIndeterminate: true,
      phoneVal: [
        { required: true, trigger: 'blur', validator: validPhone } //这里需要用到全局变量
      ],
      showBeforTime: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now()
        }
      },
      servicePeopleOpts: [], //家庭医生服务人员
      orderNamesOpts: [],
      orderStatusOpts: [],
      orderTypesOpts: [],
      orderScenesOpts: [],
      serviceDepartmentOpts: [],
      fdStatusOpts: [],
      productTypesOpts: [],
      orderActionsOpts: [],
      customSceneOpts: [],
      weappSceneOpts: []
    }
  },
  components: {
    SourceChannelSelect,
    OpratorActionSelect,
    UserTypeSelect,
    ContractStatusSelect,
    RelatedStatusSelect,
    CreatorSelect,
    CustomSceneSelect,
    WeappSceneSelect,
    SupervisorSelect,
    ServicePackage,
    OrderTypeSelect,
    OrderNameSelect,
    OrderStatusSelect,
    fdStatusSelect,
    signTypeSelect,
    serviceDepartment, //服务科室
    servicePeople //服务人员（家庭医生）
  },
  created() {
    if (this.getOrderOptsData) {
      this.getOrderOptsFun()
    }
  },
  methods: {
    getOrderOptsFun() {
      getOrderOpts().then(res => {
        this.$emit('orderBasicOpts', res.data)
        let resData = res.data
        this.orderNamesOpts = resData.order_names
        this.orderStatusOpts = resData.order_status
        this.orderTypesOpts = resData.order_types
        this.orderScenesOpts = resData.custom_scenes
        this.fdStatusOpts = resData.fd_status
        this.serviceDepartmentOpts = resData.single_ask_departments
        this.orderActionsOpts = resData.order_actions
        this.productTypesOpts = resData.product_types
        this.customSceneOpts = resData.custom_scenes
        this.weappSceneOpts = resData.weapp_scenes
      })
    },
    exportBtnFun() {
      this.$emit('exportFun')
    },
    showItem(item) {
      // 是否显此项item
      // 没有item且没有关联项时直接显示
      if (!item.haveItem && !item.cascaderItem) {
        return true
        //有关联项时再进行判断
      } else if (item.cascaderItem) {
        // 当关联项的值等于某值时显示
        if (item.cascaderValue || item.cascaderValue === false) {
          // 当某项值等于或存在某个值时显示（如：数组中存在某个值则显示）
          if (
            item.cascaderValue === this.formData[item.cascaderItem] ||
            _.indexOf(this.formData[item.cascaderItem], item.cascaderValue) !=
              -1 ||
            _.indexOf(item.cascaderValue, this.formData[item.cascaderItem]) !=
              -1
          ) {
            return true
          } else {
            return false
          }
        } else {
          // 当关联项的值不等于某个值时显示
          if (
            this.formData[item.cascaderItem] &&
            this.formData[item.cascaderItem] !== item.cascaderValueNot
          ) {
            return true
          } else {
            return false
          }
        }
      } else {
        return false
      }
    },
    selectChange($event, item) {
      this.formData[item] = $event
    },
    isvalidPhone(str) {
      const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
      return reg.test(str)
    },
    getSubmitData(clear) {
      for (let i = 0; i < this.forms.length; i++) {
        const item = this.forms[i]
        this.formData[item.ref] = {}
        for (let j = 0; j < item.formItem.length; j++) {
          item.formItem[j].value = clear ? '' : item.formItem[j].value
          this.formData[item.ref][item.formItem[j].name] =
            item.formItem[j].value
        }
      }
    },

    onSubmit() {
      this.getSubmitData()
      this.formData.sourceChannelThird = this.sourceChannelThird
      this.$emit('submit', this.formData)
    },

    onCancel() {
      this.getSubmitData(true)
      this.$emit('cancle')
    },
    handle(str, item) {
      this.$emit(str, item) //将事件名字和参数值传递出去，
    },
    submitForm() {
      this.$refs[this.form.ref].validate(valid => {
        if (valid) {
          this.$emit('submit', this.formData)
        } else {
          return false
        }
      })
    },

    resetForm() {
      this.$refs[this.form.ref].resetFields()
      this.$emit('resetForm')
    }
  }
}
</script>

