<template>
  <el-select
    :value="value"
    @change="onValueChange"
    size="small"
    clearable
    :placeholder="placeholder"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :value="item.value"
      :label="item.label"
      >{{ item.label }}</el-option
    >
  </el-select>
</template>

<script>
import { sectionOffice } from '@/utils/dictionary'
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    optionsData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      options: []
    }
  },
  mounted() {
    if (this.optionsData.length) {
      this.options = this.optionsData
    } else {
      this.options = sectionOffice('', 'arr', true)
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
