<template>
  <el-select
    :value="value"
    @change="onValueChange"
    size="mini"
    :placeholder="placeholder"
  >
    <el-option
      v-for="item in serviceDic"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    />
  </el-select>
</template>

<script>
import { getServiceDic } from '@/api/healthManagement'
export default {
  name: 'ServicePackage',
  props: {
    value: {
      type: String,
      default: ''
    },
    optionsData: {
      type: Array,
      default: () => []
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  data() {
    return {
      serviceDic: []
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    },
    getServiceDic() {
      getServiceDic().then(res => {
        if (res.code === 20000) {
          this.serviceDic = res.data
        }
      })
    }
  },
  mounted() {
    if (this.optionsData.length) {
      this.serviceDic = this.optionsData
    } else {
      this.getServiceDic()
    }
  }
}
</script>
