<template>
  <el-scrollbar
    ref="myScrollbar"
    :style="{ height: boxHeight + 'px' }"
    class="my-scrollbar"
  >
    <slot />
  </el-scrollbar>
</template>

<script>
export default {
  name: 'ScrollBar',
  props: {
    boxHeight: {
      type: Number,
      required: true
    },
    needDown: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    if (this.needDown) {
      setTimeout(this.scrollDown, 100)
    }
  },
  methods: {
    scrollDown() {
      // eslint-disable-next-line standard/computed-property-even-spacing
      this.$refs['myScrollbar'].wrap.scrollTop = this.$refs[
        'myScrollbar'
      ].wrap.scrollHeight
    }
  }
}
</script>

<style lang="scss">
.my-scrollbar {
  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
}
</style>
