<template>
  <!-- crm模块，创建人 -->
  <el-form-item :label="moduleName" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      @change="changeValue"
      :placeholder="placeholder"
      clearable
      :disabled="disabled"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
import { getCreatorOptions } from '@/api/crmSetting'
export default {
  data() {
    return {
      optionValue: '',
      options: []
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  created() {
    this.getCreatorOptionFun()
  },
  methods: {
    getCreatorOptionFun() {
      getCreatorOptions().then(res => {
        if (res) {
          this.options = res.data
        }
      })
    },
    changeValue(val) {
      this.$emit('input', val)
    }
  }
}
</script>
