<template>
  <!-- functional
    :class="['card', data.class, data.staticClass].join(' ')" -->
  <div class="card">
    <div class="card__title">
      <slot name="title">
        <span>{{ title }}</span>
      </slot>
    </div>
    <div class="card__body">
      <slot />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  created() {}
}
</script>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.card {
  padding: 26px 20px;
  border: 1px solid #ebeef5;
  border-radius: 7px;
  .el-card__body {
    padding: 30px 20px;
  }
  &__title {
    @include ff-medium;

    line-height: 22px;
    font-size: 16px;
    color: #{$textDeepBlock};
    margin-bottom: 14px;
    letter-spacing: 0;
  }
  &__body {
    @include ff-regular;

    line-height: 14px;
    font-size: 14px;
    color: #{$textLightBlock};
    letter-spacing: 0;
  }
}
</style>
