<template>
  <div>
    <div class="table-title">服务中的宝宝</div>
    <el-table
      ref="babyList"
      :data="babyList"
      style="width: 100%;"
      @row-click="setCheckedBaby"
    >
      <el-table-column label="" width="50">
        <template slot-scope="scope">
          <el-radio :label="scope.row._id" v-model="checkedBaby">
            <span hidden>&nbsp;</span>
          </el-radio>
        </template>
      </el-table-column>
      <el-table-column width="100" prop="index" label="序号" />
      <el-table-column prop="babyName" width="260px" label="宝宝">
        <template slot-scope="scope">
          <div>
            <div>{{ scope.row.name }}</div>
            <div>{{ scope.row.babyId }}</div>
          </div>
        </template>
      </el-table-column>
      <el-table-column width="260px" label="用户">
        <template slot-scope="scope">
          <div>
            <div style="display: inline-block">
              <el-avatar
                :size="'small'"
                :src="scope.row.user_info.avatar"
                style="vertical-align: middle"
              />
              {{ scope.row.user_info && scope.row.user_info.nickname }}
              <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
            </div>
            <div>{{ scope.row.user_info && scope.row.user_info._id }}</div>
          </div>
        </template>
      </el-table-column>

      <el-table-column label="出生日期">
        <template slot-scope="scope">
          {{ new Date(scope.row.birthday) | formatTime('y-m-d') }}
        </template>
      </el-table-column>
      <el-table-column prop="age_of_month" label="月龄" />
      <el-table-column width="260px">
        <template slot="header">
          <div>
            宝宝备注
            <el-tooltip
              class="item"
              effect="dark"
              content="接诊台宝宝资料处添加的备注"
              placement="top"
            >
              <i class="el-icon-warning" />
            </el-tooltip>
          </div>
        </template>
        <template slot-scope="scope">
          <span v-if="scope.row.remark">
            {{ scope.row.remark | ellipsis(10) }}
          </span>
          <span v-else>
            宝宝备注
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="serviceName" label="当前服务套餐">
        <template slot-scope="scope">
          {{
            scope.row.fd_service_type_info &&
              scope.row.fd_service_type_info.name
          }}
        </template>
      </el-table-column>
      <!--食谱宝宝列表展示健康管理师-->
      <el-table-column v-if="pageType === 'recipe_service'" label="健康管理师">
        <template slot-scope="scope">
          {{ scope.row.hm && scope.row.hm.name }}
        </template>
      </el-table-column>
      <el-table-column label="当前套餐服务时间" width="300px">
        <template slot-scope="scope">
          {{
            new Date(
              scope.row.fd_contract_info &&
                scope.row.fd_contract_info.started_at
            ) | formatTime('y-m-d')
          }}
          至
          {{
            new Date(
              scope.row.fd_contract_info && scope.row.fd_contract_info.ended_at
            ) | formatTime('y-m-d')
          }}
        </template>
      </el-table-column>
    </el-table>
    <div style=" margin-top: 30px;text-align: center ">
      <el-pagination
        background
        :current-page.sync="currentPage"
        :page-sizes="[10, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations, mapState } from 'vuex'
export default {
  name: 'BabyList',
  props: {
    pageType: {
      type: String,
      default: 'health_manager'
    }
  },
  data() {
    return {
      checkedBaby: ''
    }
  },
  computed: {
    ...mapGetters(['babyList', 'selectBaby']),
    ...mapState({
      total: state => state.health.babyCount
    }),
    currentPage: {
      get() {
        return this.$store.state.health.currentPage
      },
      set(val) {
        this.$store.commit('health/SET_CURRENTPAGE', val)
      }
    },
    pageSize: {
      get() {
        return this.$store.state.health.pageSize
      },
      set(val) {
        this.$store.commit('health/SET_PAGESIZE', val)
      }
    }
  },
  watch: {
    babyList: {
      handler: function() {
        this.clearSelection()
      },
      deep: true
    }
  },
  mounted() {
    this.setCurrentPage(1)
    this.setPageSize(10)
    const params = {
      service_item: this.pageType,
      show_replace: 1
    }
    this.getBabyList(params)
  },
  methods: {
    ...mapActions({
      getBabyList: 'health/getBabyList'
    }),
    ...mapMutations({
      setSelectBaby: 'health/SET_SELECTBABY',
      setCurrentPage: 'health/SET_CURRENTPAGE',
      setPageSize: 'health/SET_PAGESIZE'
    }),
    setCheckedBaby(row) {
      this.setSelectBaby(row)
      this.checkedBaby = row._id
    },
    clearSelection() {
      this.checkedBaby = ''
      this.setSelectBaby(null)
    },
    handleSizeChange(val) {
      this.currentPage = 1
      this.pageSize = val
      this.$emit('searchBabyList')
    },
    handleCurrentChange(val) {
      this.currentPage = val
      // 切换页码，清空选中状态，刷新宝宝列表
      this.clearSelection()
      this.$emit('searchBabyList')
    }
  }
}
</script>

<style scoped lang="scss">
.table-title {
  padding: 0 30px;
  width: 100%;
  height: 48px;
  line-height: 48px;
  color: #17233d;
  background-color: #f5f7fa;
}
</style>
