<template>
  <el-select
    :value="value"
    @change="onValueChange"
    size="small"
    clearable
    multiple
    filterable
    :placeholder="placeholder"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :value="item.value"
      :label="item.label"
      >{{ item.label }}</el-option
    >
  </el-select>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'servicePeopleSelect',
  props: {
    value: [String, Array],
    placeholder: {
      type: String,
      default: '请选择'
    },
    optionsData: {
      type: Array,
      default: () => []
    },
    peopleType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      options: []
    }
  },
  computed: {
    ...mapGetters(['allPeople'])
  },
  async mounted() {
    if (this.optionsData.length) {
      this.options = this.optionsData
    } else {
      await this.$store.dispatch('filterPeopleByRole', this.peopleType)
      this.options = this.allPeople
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
