<template>
  <div class="html2image">
    <div class="html2image__option">
      <div
        style="cursor: pointer"
        class="html2image__option__item"
        @click="previewModal = true"
      >
        图片预览
      </div>
    </div>
    <div class="html2image__source">
      <div
        id="source"
        ref="imageSource"
        style="width: 640px;"
        class="html2image__source__slot-wrapper"
      >
        <slot />
      </div>
    </div>
    <el-dialog
      title="图片预览"
      :modal="true"
      :visible.sync="previewModal"
      :width="'700px'"
      @opened="typeClickHandle('image')"
    >
      <div style="text-align: center">
        <div v-show="curActiveType === 'image'" id="image" />
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Canvas2Image from './canvas2image'

export default {
  name: 'HtmlToImage',
  props: {
    showCanvas: {
      type: Boolean,
      default: true
    },
    showImage: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      sourceContainer: null,
      curActiveType: '',
      previewModal: false
    }
  },
  computed: {
    sourceDomWidth() {
      return this.sourceContainer
        ? this.sourceContainer.offsetWidth + 32 + 'px'
        : '0px'
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.sourceContainer = document.querySelector('#source')
    })
  },
  methods: {
    htmlToCanvas(upload) {
      return new Promise((resolve, reject) => {
        if (this.sourceContainer) {
          window
            .html2canvas(this.sourceContainer, {
              useCORS: true,
              imageTimeout: 15000000000000,
              onclone: doc => {
                // 延时400ms,尝试解决图片显示不全bug
                return new Promise((resolve, reject) => {
                  setTimeout(() => {
                    resolve()
                  }, 400)
                })
              }
            })
            .then(canvas => {
              if (upload) {
                canvas.toBlob(
                  blob => {
                    resolve(blob)
                  },
                  'image/png',
                  1
                )
              } else resolve(canvas)
            })
        }
      })
    },
    async typeClickHandle(type) {
      this.curActiveType = type
      const canvas = await this.htmlToCanvas()
      const imageContainer = document.querySelector('#image')
      if (imageContainer && type === 'image') {
        imageContainer.innerHTML = ''
        imageContainer.appendChild(
          Canvas2Image.convertToImage(
            canvas,
            canvas.width,
            canvas.height,
            'png'
          )
        )
      }
    }
  }
}
</script>

<style lang="scss" scope type="text/scss">
.html2image {
  height: 100%;
  &__option {
    position: relative;
    padding-bottom: 60px;
    user-select: none;
    &__item {
      position: absolute;
      display: inline-block;
      font-weight: bold;
      color: #2d8cf0;
      background-color: #fff;
    }
    &__item + &__item {
      margin-left: 20px;
    }
  }
  &__source {
    position: relative;
  }
}
</style>
