<template>
  <el-dialog
    title="关闭任务"
    :visible.sync="closeDialogVisible"
    width="35%"
    @opened="resetReasonForm"
    :before-close="handleClose"
    :center="false"
  >
    <div class="task-close">
      <div>
        <span class="el-icon-warning"></span>
        <span> 选择原因后，单击【确认关闭】按钮关闭该任务。</span>
      </div>
      <el-form :model="reasonForm" :label-position="'top'" ref="reasonForm">
        <el-form-item
          label="请选择关闭原因"
          prop="closeReasonType"
          :rules="[
            { required: true, message: '请选择关闭原因', trigger: 'change' }
          ]"
        >
          <div class="close-select-container">
            <el-select
              v-model="reasonForm.closeReasonType"
              placeholder="请选择"
              clearable
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-form-item>

        <div v-if="reasonForm.closeReasonType === '其他'">
          <el-form-item
            label="请填写关闭原因"
            prop="closeReason"
            :rules="[
              { required: true, message: '请填写关闭原因', trigger: 'blur' }
            ]"
          >
            <el-input
              type="textarea"
              :rows="3"
              show-word-limit
              maxlength="50"
              placeholder="请输入内容"
              v-model="reasonForm.closeReason"
            >
            </el-input>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="$emit('closeDialog')">取 消</el-button>
      <el-button type="primary" @click="closeTask">确认关闭</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'index',
  props: {
    closeDialogVisible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      reasonForm: {
        closeReasonType: '',
        closeReason: ''
      },
      options: [
        {
          value: '已经在接诊台咨询过程中处理过',
          label: '已经在接诊台咨询过程中处理过'
        },
        {
          value: '已经在其他任务中统一处理过',
          label: '已经在其他任务中统一处理过'
        },
        {
          value: '其他',
          label: '其他'
        }
      ]
    }
  },
  methods: {
    closeTask() {
      // 表单验证成功，抛出关闭事件
      this.$refs.reasonForm.validate(valid => {
        if (valid) {
          this.$emit('closeTask', this.reasonForm)
        } else {
          return false
        }
      })
    },
    handleClose() {
      this.$emit('closeDialog')
    },
    resetReasonForm() {
      this.$refs.reasonForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
.task-close {
  padding: 0 30px;
  line-height: 30px;
  font-size: 14px;
}
</style>
<style lang="scss">
.close-select-container {
  width: 100%;
  .el-select {
    width: 80%;
  }
}
</style>
