<template>
  <!-- crm模块，负责人 -->
  <el-form-item :label="label" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      @change="onValueChange"
      :placeholder="placeholder"
      :disabled="selectDisable"
      clearable
    >
      <el-option
        v-for="item in options.filter(option => option.value !== filterOption)"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
import { getSupervisors } from '@/api/crmManage'
export default {
  data() {
    return {
      optionValue: '',
      options: []
    }
  },
  props: {
    selectDisable: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: '负责人'
    },
    label: {
      type: String,
      default: '负责人'
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    filterOption: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  watch: {
    value(val) {
      this.optionValue = val
    }
  },
  created() {
    this.getSupervisors()
  },
  methods: {
    getSupervisors() {
      getSupervisors().then(res => {
        if (res.code === 20000) {
          this.options = res.data
        }
      })
    },
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
