<template>
  <span>
    <el-select
      :value="value"
      @change="onValueChange"
      size="small"
      clearable
      :placeholder="placeholder"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </span>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    optionsData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      options: []
    }
  },
  watch: {
    optionsData() {
      this.setData(this.optionsData)
    }
  },
  mounted() {
    this.setData(this.optionsData)
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    },
    setData(arr) {
      if (arr.length) {
        this.options = arr
      }
    }
  }
}
</script>
