<template>
  <el-select
    :value="value"
    @change="onValueChange"
    size="small"
    clearable
    placeholder="请选择订单状态"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :value="item.value"
      :label="item.label"
      >{{ item.label }}</el-option
    >
  </el-select>
</template>

<script>
import { getOrderStatus } from '@/api/order'
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    optionsData: {
      type: Array,
      default: () => []
    },
    getRemoteData: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      options: []
    }
  },
  watch: {
    optionsData() {
      this.setData(this.optionsData)
    },
    getRemoteData(val) {
      if (val) {
        getOrderStatus().then(res => {
          this.options = res.data
        })
      }
    }
  },
  mounted() {
    this.setData(this.optionsData)
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    },
    setData(arr) {
      if (arr.length) {
        this.options = arr
      }
    }
  }
}
</script>
