<template>
  <!-- crm模块，来源渠道  @change="changeValue"-->
  <el-form-item :label="moduleName" :prop="prop" :rules="rules">
    <el-cascader
      ref="cascader"
      :value="value"
      :options="sourceChannelOptions"
      @change="changeValue"
      :multiple="multiple"
      :props="optionProps"
      :placeholder="placeholder"
      :disabled="disabled"
      collapse-tags
      clearable
    ></el-cascader>
  </el-form-item>
</template>
<script>
import _ from 'lodash'
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      optionProps: { multiple: true }
    }
  },
  props: {
    value: [Array, String],
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: true
    },
    rules: {
      type: Array,
      default: () => []
    },
    isOpen: [String, Boolean],
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  watch: {
    isOpen(val) {
      this.getOptions()
    }
  },
  computed: {
    ...mapGetters(['sourceChannelOptions'])
  },
  created() {
    this.getOptions()
  },
  methods: {
    getOptions() {
      let params = {}
      if (this.isOpen !== true && this.isOpen !== false) {
        params = {}
      } else {
        params = { is_open: this.isOpen }
      }
      this.$store.dispatch('crm/getChannelOptions', params, {
        root: true
      })
      this.optionProps.multiple = this.multiple
    },
    changeValue(val) {
      const thirdValue = _.flatMap(val, 2)
      this.$emit('input', val) //选项联动的值
      this.$emit('thirdValue', thirdValue) //三级目录
    }
  }
}
</script>
