<template>
  <!-- crm模块，关联状态 -->
  <el-form-item :label="moduleName" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      @change="changeValue"
      :placeholder="placeholder"
      clearable
      :disabled="disabled"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          label: '是',
          value: true
        },
        {
          label: '否',
          value: false
        }
      ]
    }
  },
  props: {
    value: [String, Boolean],
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请选择'
    }
  },
  created() {},
  methods: {
    changeValue(val) {
      this.$emit('input', val)
    }
  }
}
</script>
